.about p {
  @media (min-width: 1200px) {
    padding-left: 40px;
  }
}


.about__img {
  margin-bottom: 40px;
  border-radius: 5px;

  @media (max-width: 1200px) {
    margin-bottom: calc(30px + (50 - 30) * ((100vw - 320px) / (1200 - 320)));
  }
}

.about__subtitle {
  font-weight: 500;
  font-size: 18px;
  line-height: (24/18);
}


.text-md {
  display: none;

  @media (min-width: 768px) {
    display: inline;
  }
}

.color-primare {
  color: $color-primary;
}


// ===== card ===== 

@if 1 {

  .about__card {}

  .card__row {
    display: flex;
    flex-direction: column-reverse;

    @media (min-width: 768px) {
      display: block;
      margin-bottom: 30px;
    }
  }

  .card__row:after {
    content: " ";
    display: table;
    clear: both;
  }

  .card__row p {
    margin-bottom: 15px;

    @media (min-width: $sm) {
      margin-bottom: 20px;
    }

    @media (min-width: 768px) {
      margin-bottom: 0px;
    }
  }


  .card__row p {
    @media (min-width: 768px) {
      //  float: left;
      //clear:left;
    }
  }

  .card__img {
    margin-bottom: 15px;

    @media (min-width: 768px) {
      width: 50%;
      float: right;
      margin: 0px 0px 10px 20px;
      //clear:right;
    }

    @media (min-width: 1200px) {
      width: 33.333333%;
      margin-top: -85px;
    }
  }
}

.about__title-box {
  padding: 40px;
  font-weight: 500;
  font-size: 24px;
  color: $color-hover;
  background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
  box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
  border-radius: 5px;

  @media (max-width: 1200px) {
    font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1200 - 320)));
    padding: calc(15px + (40 - 15) * ((100vw - 320px) / (1200 - 320)));
  }
}



// ===== list ===== 

.about__list {
  margin-bottom: 60px;

  @media (min-width: 1200px) {
    padding-left: 40px;
  }

  @media (max-width: 1200px) {
    margin-bottom: calc(30px + (60 - 30) * ((100vw - 320px) / (1200 - 320)));
  }


  li {
    position: relative;
    padding-left: 22px;

    @media (min-width: 768px) {
      padding-left: 32px;
    }
  }

  li:not(:last-child) {
    margin-bottom: 10px;
  }

  li::before {
    content: "";
    position: absolute;
    left: 0;
    top: 4px;
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: linear-gradient(104.98deg, #6266E0 0%, #7579FF 100%);
    box-shadow: 0px 4px 4px rgba(109, 113, 249, 0.3);

    @media (min-width: 768px) {
      top: 6px;
    }
  }
}

// ===== quote =====

@if 1 {
  .quote {
    position: relative;
    padding: 40px;
    background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
    box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    border-radius: 5px;

    @media (max-width: 1200px) {
      padding: calc(15px + (40 - 15) * ((100vw - 320px) / (1200 - 320)));
    }
  }

  .quote::before,
  .quote::after {
    position: absolute;
    font-family: 'icomoon';
    font-size: 16px;
    color: $color-hover;

    @media (min-width: 768px) {
      font-size: 24px;
    }
  }

  .quote::before {
    content: "\e921";
    top: 0px;
    left: 15px;
    transform: translateY(-50%);
  }

  .quote::after {
    content: "\e922";
    bottom: 0;
    right: 15px;
    transform: translateY(50%);
  }

  .quote__text {
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 400;

    @media (max-width: 1200px) {
      font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1200 - 320)));
    }
  }

  .quote__name {
    color: $color-disable;
  }
}



// ===== about__list-num ===== 

.about__list-num {
  counter-reset: num;
  margin-bottom: 60px;

  @media (min-width: 1200px) {
    padding-left: 40px;
  }

  @media (max-width: 1200px) {
    margin-bottom: calc(30px + (60 - 30) * ((100vw - 320px) / (1200 - 320)));
  }

  li {
    position: relative;
    padding-left: 40px;

    @media (min-width: 768px) {
      padding-left: 43px;
    }
  }

  li:not(:last-child) {
    margin-bottom: 15px;
  }

  li::before {
    content: counter(num);
    counter-increment: num;
    position: absolute;
    left: 0;
    top: -3px;
    display: inline-flex;
    justify-content: center;
    width: 30px;
    height: 30px;
    font-size: 14px;
    font-weight: 500;
    line-height: 28px;
    color: $color-primary;
    border-radius: 50%;
    border: 2px solid #6D71F9;
  }
}




// ===== author ===== 

@if 1 {

  .about__author {}

  .author {
    padding: 40px;
    background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
    box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    border-radius: 5px;

    @media (min-width: 768px) {
      display: flex;
      justify-content: space-between;
    }

    @media (max-width: 1200px) {
      padding: calc(15px + (40 - 15) * ((100vw - 320px) / (1200 - 320)));
    }
  }

  .author__info {
    // @media (min-width: 768px) {
    // 	padding: 0 10px;
    // }
  }

  .author__row {
    display: flex;
    // flex-wra p: wrap;
    align-items: flex-start;
    margin-bottom: 20px;

    @media (min-width: 768px) {
      margin-bottom: 0px;
    }
  }

  .author__text {
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $color-disable;
  }

  .author__text-info {
    @media (min-width: 768px) {
      padding-left: 95px;
    }
  }

  .author__img {
    // flex: 0 0 80px;
    width: 80px;
    height: 80px;
    margin-right: 15px;
    border-radius: 50%;
    object-fit: cover;

    @media (min-width: 768px) {
      margin-top: -35px;
    }
  }

  .author__desc {
    flex: 1 1 0;
  }

  .author__name {
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
  }


  .author__link {
    @media (min-width: 768px) {
      padding-left: 20px;
    }
  }

  .author__text--link {
    @media (min-width: 768px) {
      text-align: right;
    }
  }

  .author__socials a {
    width: 40px;
    height: 40px;
    color: #fff;
    font-size: 20px;
    box-shadow: 0px 24px 24px rgba(109, 113, 249, 0.25);
    transition: .2s ease 0s;
  }

  .author__socials ul {
    justify-content: flex-start;
  }

  .author__socials li+li {
    margin-left: 10px;
  }

  .author__socials a span {
    margin-bottom: 3px;
  }
}