// ===== catalog ===== 

@if 1 {
  .shop__top {
    position: relative;
    padding: 20px 0px 5% 0px;
    background-image: url(../img/bg.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom left;
  }

  .shop__title {
    margin-bottom: 20px;
    text-align: center;
    color: #fff;
  }

  .shop__input {
    font-size: 14px;
  }

  .input__wrap {
    display: flex;
    margin-bottom: 40px;
  }

  .shop__btn-submit {
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    margin-left: 10px;
  }

  .shop__btn-submit span {
    display: none;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .shop__input:hover {
      color: #2A2929;
      background-color: #fff;
    }
  }

  .shop__input:focus {
    color: #2A2929;
    background-color: #fff;
  }



  .filter {
    display: none;
    font-size: 14px;
  }

  .filter__row {
    margin-bottom: 20px;
  }

  .icon-down {
    display: none;
  }

  .shop-pin__btn.active .icon-down {
    display: block;
  }

  .shop-pin__btn.active .icon-equalizer {
    display: none;
  }





  .filter__title {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 24px;
    color: $color-hover;
  }

  @media (min-width: 480px) {
    .filter {
      font-size: 16px;
    }

    .filter__row {
      margin-bottom: 0px;
    }

    .filter__title br {
      display: none;
    }
  }

  @media (min-width: 768px) {
    .shop__btn-submit {
      width: 166px;
      height: 60px;
    }

    .shop__btn-submit img {
      display: none;
    }

    .shop__btn-submit span {
      display: block;
    }

    .filter {
      font-size: 18px;
    }
  }

  @media (min-width: 1200px) {
    .shop {
      padding: 0px 0px 2% 0px;
    }

    .shop__btn-submit {
      width: 232px;
    }

    .filter {
      display: block;
    }

    .shop__pin {
      display: none;
    }
  }

}



// ===== filter check ===== 

.filter__label-check {
  color: #fff;
}

.filter__label-check::before {
  background-color: rgba(#fff, 0.1);
  box-shadow: -12px -12px 24px #7d82ff, 12px 12px 24px #5d60d4;
}



// ===== category ===== 

@if 1 {
  .category {
    padding: 50px 0px 0px 0px;
  }

  .category__row {
    margin: 0 -5px;
    margin-bottom: 30px;
  }

  .category__col {
    padding: 0 5px;
    margin-bottom: 25px;
  }

  .category__item {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 125px;
    box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    border-radius: 5px;
  }

  .category-item__top {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 0px 15px 10px 15px;
    background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .category__item:hover {
      box-shadow: 0px 12px 12px rgba(109, 113, 249, 0.25);
    }

    .category__item:hover .category-item__top {
      background-color: #fff;
      background-image: none;
    }

    .category__item:hover .category__link {
      background-color: #F5F5F8;
    }
  }


  .category__icon {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
  }

  .category__icon img {
    margin-top: -15px;
    margin-right: 10px;
  }

  .category__icon img:last-child {
    margin-right: 0px;
  }


  .category-item__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .category__title {
    margin-bottom: 11px;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    // text-align: center;
    text-transform: uppercase;
  }

  .category__rating {
    font-size: 14px;
  }

  @media (min-width: 480px) {
    .category__row {
      margin-bottom: 40px;
    }

    .category__item {
      min-height: 210px;
    }

    .category__rating {
      justify-content: center;
    }

    .category-item__row {
      flex-grow: 1;
      flex-direction: column;
      align-items: center;
    }
  }


  .rating-show {
    display: flex;
  }

  .rating-show span {
    color: #D3D4FD;
    line-height: 1;
    // letter-spacing: 10px;
  }

  .rating-show span.active {
    color: #6D71F9;
  }

  .rating-show>span:before {
    content: "★";
    display: block;
  }


  .category__img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 15px;
  }

  .category__img img {
    max-width: 70px;
  }

  .category__link {
    flex-shrink: 0;
    display: block;
    padding: 6px 15px;
    color: #B8B9D0;
    text-align: center;
    font-size: 14px;
    line-height: 17px;
    background-color: #fff;
  }

  @media (min-width: 480px) {
    .category__img {
      flex-grow: 1;
    }

    .category__img img {
      max-width: 100%;
    }
  }


}




// ===== more =====

@if 1 {
  .more__btn {
    font-size: 14px;
    color: #B8B9D0;
    cursor: pointer;
  }

  .more__btn.hidden {
    display: none;
  }

  .more p:nth-of-type(n+2) {
    display: none;
  }

  @media (min-width: 768px) {
    .more p:nth-of-type(-n+2) {
      display: block;
    }
  }

  @media (min-width: 1200px) {
    .more p:nth-of-type(n+1) {
      display: block;
    }

    .more__btn {
      display: none;
    }
  }
}




// ===== select 1 filter ===== 

@if 1 {
  .select1-wrap:after {
    border-top-color: #fff;
  }

  .select1 {
    box-shadow: -12px -12px 24px #7D82FF, 12px 12px 24px #5D60D4;
  }

  .select1,
  .select1-custom__btn {
    color: #fff;
    background-color: #7D82FF;
  }

  .select1:invalid,
  .select1-custom__btn.invalid {
    color: rgba(255, 255, 255, 0.5);
  }


  // === select-custom options ===

  .select1-custom__options {
    border: 1px solid #6f6f6f;
  }

  .select1-custom__option:not(:last-of-type)::after {
    border-bottom: 1px solid #d3d3d3;
  }
}




// ==== select 2 catalog ==== 

@if 1 {

  .select2-wrap:after,
  .select2-custom {
    z-index: 2;
  }

  // arrow
  .select2-wrap:after {
    border-top-color: #B8B9D0;
  }


  .select2,
  .select2-custom__btn {
    background-color: #fff;
  }

  .select2:invalid,
  .select2-custom__btn.invalid {
    color: #B8B9D0;
  }


  @media (min-width: 1200px) {
    .select-xl-wrap:after {
      display: none;
    }

    .select-xl,
    .select-xl-custom__btn {
      position: absolute;
      top: 0px;
      left: 0px;
    }

    .select-xl-custom__btn {
      opacity: 0;
    }

    .select-xl-custom {
      position: relative;
      height: auto;
    }

    .select-xl-custom__options {
      position: static;
      display: block;
      padding: 0;
      background-color: #F5F5F8;
    }

    .select-xl-custom__option {
      padding: 15px 5px 15px 30px;
      border-radius: 30px;
    }

    // убрать залипание фокуса
    .select-xl-custom__option.isHover {
      background-color: transparent;
      color: #2A2929;
    }

    .select-xl-custom__option.isActive {
      color: #fff;
      background-image: linear-gradient(104.98deg, #6266E0 0%, #7579FF 100%);
      box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    }

    .select-xl-custom__option.isActive::before {
      color: #fff;
    }


    @media (pointer: fine),
    (min-width:0\0) {

      .select-xl-custom__option:not(.isActive):hover {
        color: $color-hover;
      }
    }

    .select-xl-custom__option:not(.isActive):active {
      color: $color-active;
    }
  }
}