
// ====  modal =====

.no-scroll {
  overflow-y: auto !important;
}

.has-dialog {
  overflow: hidden;
}


// modal backdrop

.dialog-backdrop {
  display: none;
  position: fixed;
  z-index: 100;
  overflow-y: auto;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background: #F5F5F8;
  @media (min-width: 768px) {
    position: static;
     display: block;
  }
}

.dialog-backdrop.active {
  display: block;
}


