}

.modal {
  padding: 90px 15px 15px 15px;
}

.modal__btn-close {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  padding-left: 15px;
  font-size: 20px;
  text-align: left;
  background-color: #fff;
  @media (min-width: 768px) {
     display: none;
  }
}
.modal__btn-close::before {
  margin-right: 15px;
  color: #6D71F9;
}

.modal__icon-close {
  width: 14px;
  height: 14px;
  color: #ffffff;
}

.title_2--modal {
  font-size: 25px;
}

@media (min-width: 400px) {
  .title_2--modal {
    font-size: 36px;
  }
}

@media (min-width: 768px) {
  .modal {
    padding: 0px;
  }

  .modal__btn-close {
    top: 0px;
    right: -70px;
    width: 70px;
    height: 70px;
  }
}

.modal__text {
  margin-bottom: 30px;
}

.form__input--modal {
  margin-bottom: 20px;
  border: 2px solid #eaeaea;
  background-color: #f7f7f7;
}

.form__input--modal:focus {
  background-color: #f7f7f7;
  border-color: #4976f6;
}

.form__input--modal:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 70px #f7f7f7 inset;
  -webkit-text-fill-color: #70737a;
}

.form__textarea {
  height: 90px;
  padding: 16px 20px;
}