
// icon plus

.i-plus::before,
.i-plus::after {
  content: '';
  position: absolute;
  right: 15px;
  top: 24px;
  display: block;
  width: 12px;
  height: 2px;
  background-color: $color-primary;
  transition: 0.2s ease 0s;

  @media (min-width: 768px) {
    top: 26px;
    right: 30px;
  }
  @media (min-width: 1200px) {
     display: none;
  }
}

.i-plus:after {
  transform: rotate(90deg);
}

.i-plus.active:after {
  transform: rotate(0deg);
}


// svg sprite

.icon {
	display: inline-block;
	width: 36px;
	height: 36px;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor;
}



[class^="icon-"]::before,
 [class*=" icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: middle;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

