// ===== cabinet ===== 

.user {
  padding-bottom: 60px;
  @media (min-width: 1200px) {
     padding-bottom: 120px;
  }
}

.user__link {
  padding: 0 20px;

  @media (min-width: 768px) {
    display: none;
  }
}

// icon arrow

.user__link::before {
  position: absolute;
  right: 15px;
  top: 19px;
  color: $color-primary;
}




// ===== tabs ===== 

@if 1 {
  .tabs {
    display: none;

    @media (min-width: 768px) {
      display: block;
    }
  }

  .tabs__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
    box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    border-radius: 30px;
  }

  .tabs__btn {
    flex: 1 0 0;
    position: relative;
    height: 60px;
    border-radius: 30px;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .tabs__btn:hover {
      background-color: #fff;
    }
  }


  .tabs__panel {
    display: none;
  }

  .tabs__panel.active {
    display: block;
  }
}



// ===== spoiler ===== 

@if 1 {
  .spoiler-user {
    @media (min-width: 1200px) {
      display: flex;
      flex-wrap: wrap;
    }
  }

  .spoiler-user__list {
    @media (min-width: 1200px) {
      width: 33.333333%;
      padding: 0 10px;
    }

    @media (min-width: 1270px) {
      width: 25%;
      margin-right: 8.333333%;
    }
  }

  .spoiler-user__btn {
    padding: 0 30px;

    @media (min-width: 1200px) {
      margin-bottom: 0;
      background: none;
      box-shadow: none;

      @media (pointer: fine) {
        &:hover {
          color: $color-hover;
        }
      }
    }

  }

  .spoiler-user__btn.active {
    color: $color-primary;

    @media (min-width: 1200px) {
      color: #fff;
      background: linear-gradient(104.98deg, #6266E0 0%, #7579FF 100%);
      box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    }
  }

  .spoiler-user__icon {
    margin-right: 10px;
    color: $color-disable;

    @media (min-width: 1200px) {
      margin-right: 20px;
    }
  }


  .spoiler-user__group {
    @media (min-width: 1200px) {
      width: 66.66667%;
      padding: 0 10px;
    }
  }

  .spoiler-user__panel {
    display: none;
    padding: 40px 0;
  }

  .spoiler-user__panel.active {
    display: block;
  }

  .btn-add-shop {
    min-height: 40px;
    width: 100%;
    color: $color-primary;
    border: 2px solid #6D71F9;
    border-radius: 20px;

    @media (min-width: 1200px) {
      width: auto;
      min-height: 60px;
      border: none;
    }

    &::before {
      margin-right: 20px;
    }
  }
}




// ===== Юр.лица ===== 


.user__right {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin-bottom: 40px;
  padding: 0 30px;
  background-color: rgba(92, 222, 218, .1);
  border-radius: 30px;
  color: #5CDEDA;
}



// ===== gallery =====

@if 1 {
  .user__gallery {
    margin-bottom: 40px;
  }

  .user__wrap-img {
    display: block;
    padding-bottom: 60%;
  }




  .btn-add-photo {
    width: 100%;
    height: 100%;
    display: block;
    border: 2px solid #6D71F9;
    border-radius: 5px;
    transition: .2s ease 0s;
  }

  .btn-add-photo span::before {
    color: $color-primary;
    display: block;
    margin-bottom: 20px;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .btn-add-photo:hover {
      border-color: $color-hover;
    }

    .btn-add-photo:hover span::before {
      color: $color-hover;
    }
  }
}


.user__info {
  margin-bottom: 36px;
}



// ==== select 3 cabinet ==== 

@if 1 {
  .select3-wrap {
    margin-bottom: 0;

    @media (min-width: 768px) {
      font-size: 18px;
    }
  }

  // arrow
  .select3-wrap:after {
    border-top-color: #B8B9D0;
  }

  .select3,
  .select3-custom__btn {
    background-color: #fff;
  }

  .select3:invalid,
  .select3-custom__btn.invalid {
    color: #B8B9D0;
  }
}



// ===== point ===== 

@if 1 {
  .point__title-row {
    display: flex;
    // flex-wrsap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .point__btn-add {
    width: auto;
    padding-left: 30px;
    padding-right: 30px;
  }

  .tel__col {
    display: flex;
    align-items: center;
  }

  .tel__input {
    flex: 1 0 auto;
    width: auto;
  }

  .btn-del {
    // display: block;
    width: 14px;
    // margin-left: 15px;
    // margin-bottom: 10px;
    color: $color-primary;

    &:before {
      font-size: 14px;
    }
  }

  .btn-add {
    flex: 0 0 auto;
    align-self: flex-end;
    padding: 0 10px;
    margin-bottom: 20px;
    color: $color-primary;

    &::before {
      margin-right: 20px;
    }
  }

  .point__work {
    margin-bottom: 30px;
  }

  .work__item {
    display: flex;
    margin-bottom: 0px;
  }
}


// =====  check ===== 

@if 1 {
  .check {
    flex-shrink: 0;
    width: 200px;
    padding-top: 17px;
  }

  .user__label-check::before {
    background-color: #fff;
  }

  .check-2 {
    padding-left: 34px;
  }

  .check__input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }


  .check__box {
    position: absolute;
    left: 0;
    top: 19px;
    width: 24px;
    height: 24px;
    background: center/100%;
    background-color: #fff;
    border-radius: 4px;
  }

  .check__input:checked+.check__box {
    background-image: url(../icon.svg#icon-check),
      linear-gradient(180deg, #5cdeda 0%, #2fff6a 100%);
  }

  .check__input:focus.focus+.check__box {
    box-shadow: white 0 0 0 2px, lightskyblue 0 0 0 5px;
  }

  @media (pointer: fine) {
    .check-2:hover .check__box {
      border: 2px solid #5cdeda;
    }
  }


  .check__input:disabled+.check__box {
    box-shadow: 0 0 0 0.1em #9B9B9B;
  }

  .check__input:checked:disabled+.check__box {
    background-color: #9B9B9B;
  }
}



// ===== time input ===== 

@if 1 {

  .time {
    align-items: center;
    width: 100%;
  }

  .time__item {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    // flex-wrap: wrap;
  }

  .time__item span {
    color: $color-primary;
  }

  .time__input {
    width: auto;
    font-size: 18px;
    // width: 110px;
  }

  .time__input::-webkit-datetime-edit {
    padding: 0 7px;
  }

  .time__input::-webkit-datetime-edit-fields-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // Hour Minute 
  .time__input::-webkit-datetime-edit-hour-field,
  .time__input::-webkit-datetime-edit-minute-field {
    border-radius: 15%;
    padding: 10px 4px;
  }

  .time__input::-webkit-datetime-edit-hour-field:focus,
  .time__input::-webkit-datetime-edit-minute-field:focus {
    color: #2A2929;
    background-color: #f2f4f5;
  }

  // :
  .time__input::-webkit-datetime-edit-text {
    padding: 0 2px;
  }

  // 'X' button
  .time__input::-webkit-clear-button {
    display: none;
  }

  // Up/Down arrows 
  .time__input::-webkit-inner-spin-button {
    display: none;
  }

  // clock
  .time__input::-webkit-calendar-picker-indicator {
    // opacity: 0;
    display: none;
  }


}


.form__text {
  height: 120px;
  padding: 20px 30px;
}




