// ===== input =====

input[type='text'],
input[type='email'],
input[type='tel'],
textarea {
  -webkit-appearance: none;
  appearance: none;
}

textarea {
  resize: none;
  padding: 10px 10px;
}

// сброс при автозаполнении

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 70px #8185fd inset;
  // -webkit-box-shadow: 10px 20px 10px rgba(0, 0, 0, 0.4) inset;
  -webkit-text-fill-color: #fff;
}

// каретка

input,
textarea,
[contenteditable] {
  // caret-color: #70737a;
}

input::placeholder {
  font-family: 'Rubik', sans-serif;
  color: #b8b9d0;
  opacity: 1;
  text-overflow: ellipsis;
}

input:hover::placeholder,
input:focus::placeholder {
  color: #b8b9d0;
}

.form__input.invalid {
  // background-color: #f2f5f8;
  border: 2px solid #eb5757;
  // border-radius: 5.0px;
  transition: .2s ease 0s;
}

.form__item .error {
  position: absolute;
  // left: 0;
  // bottom: 0;
  // transform: translateY(100%);
  font-size: 14px;
  line-height: 19px;
  color: #eb5757;
  opacity: 0;
  transition: .2s ease 0s;
}

.form__item .error.active {
  opacity: 1;
}

.form__item .error::before {
  content: url(../img/alert.svg);
  vertical-align: middle;
  margin-right: 10px;
}

