// ===== checkbox ===== 

@if 1 {
  .check__input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }

  .check__label {
    position: relative;
    display: block;
    padding-left: 34px;
    margin-bottom: 10px;
    // color: #fff;
    cursor: pointer;
  }

  .check__label::before {
    content: '';
    position: absolute;
    left: 0;
    top: 2px;
    width: 24px;
    height: 24px;
    border-radius: 4px;
    // background-color: rgba(#fff, 0.1);
    // box-shadow: -12px -12px 24px #7d82ff, 12px 12px 24px #5d60d4;
  }

  .check__input:checked+.check__label:before {
    background: center/100%;
    background-image: url(../icon.svg#icon-check),
      linear-gradient(180deg, #5cdeda 0%, #2fff6a 100%);
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .check__label:hover::before {
      border: 2px solid #5cdeda;
    }
  }
}




// ===== form shop =====

@if 1 {
  .form__wrap {
    display: grid;
    gap: 0 20px;
    margin-bottom: 20px;

    @media (min-width: 768px) {
      grid-template-columns: 1fr 1fr;
    }

    @media (min-width: 1200px) {
      grid-template-columns: 1fr 2fr;
    }
  }

  .form__item {
    position: relative;
  }

  .form__item-required::before,
  .form__item-rating::before {
    position: absolute;
    top: 10px;
    right: 12px;
    content: '★';
    display: block;
    font-size: 15px;
    // letter-spacing: 10px;
    line-height: 1;
    color: #5cdeda;
  }

  .form__item-textarea {
    @media (min-width: 768px) {
      grid-column: span 2;
    }

    @media (min-width: 1200px) {
      grid-column: span 1;
      grid-row: span 2;
    }
  }

  .form__item-rating {
    @media (min-width: 768px) {
      grid-column: 2;
      grid-row: 1;
    }

    @media (min-width: 1200px) {
      grid-column: 1;
      grid-row: 3;
    }
  }

  .form__item-title {
    @media (min-width: 1200px) {
      grid-column: 2;
      grid-row: 1;
    }
  }

  .form__input {
    display: block;
    width: 100%;
    height: 60px;
    padding: 0px 20px;
    border-radius: 30px;
    border: 2px solid transparent;
    transition: .2s ease 0s;
  }

  .form__input:focus {
    border-color: #5cdeda;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .form__input:hover {
      border-color: #5cdeda;
    }
  }

  .form__textarea {
    min-height: 100px;
    padding-top: 20px;
    padding-bottom: 10px;

    @media (min-width: 768px) {
      height: 100%;
    }
  }

  .form__row {
    @media (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  .form_2 p {
    margin-bottom: 20px;
    font-size: 14px;

    @media (min-width: 768px) {
      margin-bottom: 0;
    }
  }

  .form__btn {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
  }

  .btn-cancel-md {
    display: none;
    margin: 0 20px;

    @media (min-width: 768px) {
      display: block;
    }

    @media (min-width: 1200px) {
      display: none;
    }

    @media (pointer: fine),
    (min-width:0\0) {
      &:hover {
        color: $color-hover;
      }
    }
  }

  @media (min-width: 768px) {
    .btn_submit {
      width: auto;
      margin-left: 20px;
    }
  }
}



// ===== form cabinet ===== 

@if 1 {
  .form-3 {
    display: flex;
    flex-wrap: wrap;
  }

  .form__item {
    position: relative;
    margin-bottom: 20px;
  }

  .form__item::before {
    position: absolute;
    right: 30px;
    bottom: 20px;
  }

  .form__label {
    display: inline-block;
    margin-bottom: 10px;
  }

  .form-3__btn {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-top: 20px;
  }

  .btn-cancel {
    margin-right: 40px;
    color: $color-primary;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .btn-cancel:hover {
      color: $color-hover;
    }
  }
}