// ===== rating =====

.rating {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  min-height: 60px;
  padding: 0 20px;
  // margin-bottom: 20px;
  background-color: #fff;
  border-radius: 30px;
  @media (min-width: 768px) {
    padding: 0 30px;
  }
}

.rating__group {
  display: inline-flex;
  padding-top: 5px;
}

.rating__group.focus {
  box-shadow: white 0 0 0 2px, lightskyblue 0 0 0 5px;
}

.rating__label:first-of-type {
  // display: none;
}

.rating__input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.rating__input:focus.focus + .rating__label {
  box-shadow: none;
}

.rating__label {
  cursor: pointer;
}

.rating__label:before {
  content: url(../img/star.svg);
  margin-right: 5px;
}

.rating__label:last-child::before {
  margin-right: 0;
}

.rating__input:checked + .rating__label ~ .rating__label:before {
  content: url(../img/star-bg.svg);
}

@media (pointer: fine), (min-width: 0\0) {
  .rating__group:hover .rating__label[for]::before {
    content: url(../img/star.svg);
  }

  .rating__group:hover .rating__label[for]:hover::before {
    content: url(../img/star-hover.svg);
  }

  .rating__group:hover .rating__label:hover ~ .rating__label:before {
    content: url(../img/star-bg.svg);
  }
}
