// ===== index =====

@if 1 {
  .index {
    overflow: hidden;
  }

  .index__wrap {
    position: relative;
    z-index: 2;
    filter: drop-shadow(0px 18px 18px rgba(109, 113, 249, 0.4));
  }


  .webp .index__top {
    background-image: url(../img/w-bg9.webp), url(../img/bg.svg);
  }

  .no-webp .index__top {
    background-image: url(../img/w-bg9.png), url(../img/bg.svg);
  }

  .webp .index__top,
  .no-webp .index__top {
    padding: 30px 0px 10% 0px;
    text-align: center;
    background-repeat: no-repeat;
    background-position: top 150px right 25%, bottom left;
    background-size: 250%, 100% auto;

    @media (min-width: 480px) {
      padding: 40px 0px 5% 0px;
    }

    @media (min-width: 768px) {
      padding: 100px 0px 9% 0px;
      text-align: left;
      background-position: left -22vw top, bottom left;
      background-size: 150%, 100% auto;
    }

    @media (min-width: 1200px) {
      padding: 190px 0px 7% 0px;
      background-position: center right, bottom right;
      background-size: 100% auto;
    }

    @media (min-width: 1700px) {
      background-position: bottom right, bottom right;
    }
  }



  .index__title {
    margin-bottom: 17px;
  }

  .index__text {
    color: #fff;
    margin-bottom: 175px;
  }

  .index__text span {
    color: #5cdeda;
  }

  @media (min-width: 480px) {
    .index__text {
      margin-bottom: 240px;
    }
  }

  @media (min-width: 768px) {
    .index__text {
      margin-bottom: 60px;
    }
  }



  .form_1 {
    margin-bottom: 15px;
  }

  .input {
    width: 100%;
    height: 50px;
    padding: 0 20px;
    margin-bottom: 15px;
    color: #fff;
    background-color: rgba(#fff, 0.1);
    // box-shadow: -12px -12px 24px #7D82FF, 12px 12px 24px #5D60D4;
    border-radius: 30px;
  }

  .index__btn-img {
    display: none;
  }

  @media (min-width: 480px) {
    .form_1 {
      display: flex;
      margin-bottom: 40px;
    }

    .input {
      margin-bottom: 0;
    }

    .index__btn-submit {
      flex-shrink: 0;
      width: 50px;
      height: 50px;
      margin-left: 10px;
    }

    .index__btn-img {
      display: block;
    }

    .index__btn-submit span {
      display: none;
    }
  }

  @media (min-width: 768px) {
    .input {
      height: 60px;
    }

    .index__btn-img {
      display: none;
    }

    .index__btn-submit {
      width: 25%;
      margin-left: 20px;
    }

    .index__btn-submit span {
      display: inline;
    }
  }

  .index__item {
    position: relative;
    display: block;
    padding: 10px 15px 10px 60px;
    text-align: center;
    min-height: 60px;
    margin-bottom: 15px;
    color: #fff;
    border: 4px solid transparent;
    border-radius: 30px 5px 5px 30px;
    background: linear-gradient(104.98deg, #6266e0 0%, #7579ff 100%);
    background-origin: border-box;
    box-shadow: -12px -12px 24px #7d82ff, 12px 12px 24px #5d60d4;
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .index__item:hover {
      color: #6d71f9;
      background-image: linear-gradient(180deg, #5cdeda 0%, #2fff6a 100%),
        linear-gradient(0deg, #5cdeda 0%, #2fff6a 100%);
      background-clip: padding-box, border-box;
    }

    .index__item:hover .index-item__text {
      opacity: 0;
    }

    .index__item:hover .index-item__title::after {
      content: "";
      display: block;
      width: 15px;
      height: 20px;
      margin-left: 15px;
      background-image: url(../img/icons/icon.svg);
    }
  }

  .index__item:active {}

  .index__item span {
    color: #5cdeda;
  }

  .index-item__img {
    position: absolute;
    top: 50%;
    left: 0%;
    width: 60px;
    height: 60px;
    transform: translate(0%, -50%);
    border: 4px solid #6d71f9;
    border-radius: 50%;
  }

  .index-item__title {
    position: relative;
    display: flex;
    justify-content: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
  }

  .index-item__text {
    font-size: 14px;
    line-height: 17px;
  }

  @media (min-width: 480px) {
    .index__item {
      padding: 0px 15px 15px 15px;
      margin-bottom: 40px;
      border-radius: 5px;
    }

    .index-item__img {
      position: static;
      margin-top: -26px;
      margin-bottom: 4px;
      transform: translate(0);
    }

    .index-item__title {
      margin-bottom: 8px;
    }
  }

  @media (min-width: 768px) {
    .index-item__img {
      margin-bottom: 14px;
    }
  }

  .index__pin {
    position: absolute;
    z-index: 1;
    bottom: 0%;
    left: 50%;
    transform: translate(-50%, 50%);
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background: #6d71f9;
  }

  .index-pin__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    color: $color-hover;
    background: $color-primary;
    box-shadow: 0px 12px 12px #5d60d4;
    border-radius: 50%;
    transition: 0.2s ease 0s;
  }

  .index-pin__icon {
    font-size: 30px;
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .index-pin__btn:hover {
      background-color: #5cdeda;
      color: #6d71f9;
    }

    .index-pin__btn:hover .index-pin__icon::before {
      color: #6d71f9;
    }
  }

  @media (min-width: 768px) {
    .index__pin {
      width: 90px;
      height: 90px;
    }

    .index-pin__btn {
      width: 60px;
      height: 60px;
    }
  }
}

// ===== map =====

@if 1 {
  .map {
    width: 100%;
    height: 480px;
    margin-top: -400px;
    transition: 0.9s ease 0s;

    @media (min-width: 480px) {
      height: 580px;
      margin-top: -470px;
    }

    @media (min-width: 768px) {
      height: 680px;
      margin-top: -550px;
    }

    @media (min-width: 1200px) {
      height: 880px;
      margin-top: -700px;
    }
  }

  .map.active {
    margin-top: -10%;
  }

  .map .gm-style-pbc+div {
    background-image: linear-gradient(180deg, rgba(245, 245, 248, 0) 70%, #f5f5f8 100%);
  }

  .mainmarker {
    z-index: 4;
    width: 68px;
    height: 76px;
    background-image: url(../img/mainMarker.svg);
    cursor: pointer;
    transform: translate(-50%, -100%);
  }

  .marker {
    z-index: 4;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 40px;
    padding-bottom: 4px;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    background-image: url(../img/pin.svg);
    cursor: pointer;
    transform: translate(-50%, -100%);
  }

  // === maps-infoWindow ===

  //.gm-ui-hover-effect {display: none !important;}  отключить кнопку закрытия*/
  //.gm-style-iw {text-align:center;}  центрировать текст после удаления кнопки закрытия

  .infowindow .close {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 10px;
    color: #b8b9d0;
    cursor: pointer;
  }

  .infowindow .close::before {
    content: "✖";
    font-size: 18px;
    line-height: 1;
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .infowindow .close:hover {
      color: #fff;
    }
  }

  .infowindow {
    z-index: 5;
    padding: 20px;
    color: #fff;
    font-size: 14px;
    line-height: 24px;
    border-radius: 4px;
    background-color: #fff;
    background: linear-gradient(104.98deg, #6266e0 0%, #7579ff 100%);
    box-shadow: 0px 12px 12px rgba(109, 113, 249, 0.25);
    visibility: hidden;
    transform: translate(-50%, -20%);

    @media (min-width: 768px) {
      transform: translate(-50%, -100%);
    }
  }

  // arrow
  @media (min-width: 768px) {
    .infowindow:after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      height: 0;
      width: 0;
      pointer-events: none;
      border: solid transparent;
      border-top-color: $color-primary;
      border-width: 10px;
    }
  }

  .infowindow__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: $color-hover;
  }

  .infowindow__desc {
    position: relative;
    padding-left: 20px;
  }

  .infowindow__desc::before {
    position: absolute;
    top: 5px;
    left: 0px;
  }
}

// ===== brand =====

@if 1 {
  .brand {
    padding-bottom: 60px;
  }

  .brand__container {
    position: relative;
    padding: 0;
  }

  .brand__title {
    margin-bottom: 30px;
    text-align: center;
  }

  .brand__slider-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    color: $color-primary;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 10px;
  }

  .brand__list {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-auto-flow: dense;
    padding: 20px 20px;
  }

  .brand__list li {
    position: relative;
    border-radius: 5px;
    background: linear-gradient(109.5deg, #eeeef1 6.26%, #ffffff 93.74%);
    box-shadow: -12px -12px 24px #ffffff, 12px 12px 24px #e9e9ec;
  }

  .brand__list a {
    display: block;
  }

  .brand__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .brand__info {
    position: absolute;
    top: 0%;
    left: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 16px 5px 20px 20px;
    color: #fff;
    opacity: 0;
    border-radius: 5px;
    background-image: linear-gradient(104.98deg, #6266e0 0%, #7579ff 100%);
    transition: 0.2s ease 0s;
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .brand__info:hover {
      opacity: 1;
    }
  }

  .brand__list li:nth-child(2) {
    padding-bottom: 60%;
  }

  .brand__list li:nth-child(3) {
    grid-column: span 2;
  }

  @media (min-width: 480px) {
    .brand__list {
      grid-template-columns: repeat(3, 1fr);
    }

    .brand__list li:nth-child(3) {
      grid-column: span 1;
    }

    .brand__list li:nth-child(4) {
      grid-column: span 2;
    }

    .brand__list li:nth-child(5) {
      grid-row: span 2;
    }
  }

  @media (min-width: 768px) {
    .brand__list {
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
    }

    .brand__list li:nth-child(1) {
      grid-row: span 2;
    }

    .brand__list li:nth-child(3) {
      grid-row: span 1;
    }

    .brand__list li:nth-child(4) {
      grid-column: span 1;
    }

    .brand__list li:nth-child(5) {
      grid-row: span 1;
    }

    .brand__list li:nth-child(7) {
      grid-row: span 2;
    }

    .brand__list li:nth-child(9) {
      grid-column: span 2;
    }
  }

  @media (min-width: 1200px) {
    .brand__list {
      grid-template-columns: repeat(5, 1fr);
    }

    .brand__list li:nth-child(1) {
      grid-row: span 1;
    }

    .brand__list li:nth-child(4) {
      grid-column: span 2;
    }

    .brand__list li:nth-child(5) {
      grid-column: span 2;
    }

    .brand__list li:nth-child(6) {
      grid-row: span 2;
    }

    .brand__list li:nth-child(7) {
      grid-row: span 1;
    }

    .brand__list li:nth-child(9) {
      grid-column: span 1;
    }
  }

  @media (min-width: 1380px) {
    .brand__slider-control {
      justify-content: center;
    }

    .brand__prev,
    .brand__next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .brand__prev {
      left: -30px;
    }

    .brand__next {
      right: -30px;
    }
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .btn-slider:hover {
      color: $color-hover;
    }
  }

  .btn-slider:active {
    color: $color-active;
  }

  .btn-slider.slick-disabled {
    color: $color-disable;
    cursor: default;
  }
}

// ===== accordion =====

@if 1 {
  .accord {
    /* margin: 30px; */
  }

  .accord__btn {
    position: relative;
    display: block;
    padding: 1em 1.5em;
    width: 100%;
    text-align: left;
  }

  .accord__btn:focus {
    background: hsl(216, 94%, 94%);
  }

  .accord__title {
    display: block;
    padding: 0.25em;
    border: transparent 2px solid;
    border-radius: 5px;
    pointer-events: none;
  }

  .accord__btn:focus .accord__title {
    border-color: hsl(216, 94%, 73%);
  }

  .accord__icon {
    position: absolute;
    top: 50%;
    right: 2em;
    height: 0.5rem;
    width: 0.5rem;
    border: solid hsl(0, 0%, 62%);
    border-width: 0 2px 2px 0;
    pointer-events: none;
    transform: translateY(-60%) rotate(45deg);
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .accord__btn:hover {
      background: hsl(216, 94%, 94%);
    }

    .accord__btn:hover .accord__icon {
      border-color: red;
    }
  }

  .accord__btn:focus .accord__icon {
    border-color: red;
  }

  .accord__btn[aria-expanded="true"] .accord__icon {
    transform: translateY(-50%) rotate(-135deg);
  }

  .accord__panel {
    height: 0px;
    padding-left: 15px;
    padding-right: 15px;
    overflow: hidden;
    transition: height 0.5s;
  }
}