// ===== title =====

.title_1,
.title_2,
.title_3,
.title_4,
.title_5,
.title_6 {
  font-weight: 500;
  margin-bottom: 40px;
}

.title_1 {
  color: #fff;
  font-size: 70px;
  line-height: (80/70);
}

.title_2 {
  font-size: 50px;
  line-height: (60/50);
}

.title_3 {
  font-size: 46px;
  line-height: (56/46);
}

.title_4 {
  font-size: 40px;
  line-height: (50/40);
}

.title_5 {
  font-size: 36px;
  line-height: (46/36);
}

.title_6 {
  font-size: 32px;
  line-height: (42/32);
}

@media (max-width: 1200px) {
  .title_1 {
    font-size: calc(30px + (70 - 30) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_2 {
    font-size: calc(26px + (50 - 26) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_3 {
    font-size: calc(24px + (46 - 24) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_4 {
    font-size: calc(22px + (40 - 22) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_5 {
    font-size: calc(20px + (36 - 20) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_6 {
    font-size: calc(19px + (32 - 19) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_1,
  .title_2,
  .title_3,
  .title_4,
  .title_5,
  .title_6 {
    margin-bottom: calc(15px + (40 - 15) * ((100vw - 320px) / (1200 - 320)));
  }
}



.subtitle {
  font-weight: 600;
  color: #4976f6;
  margin-bottom: 9px;
}