// ===== store =====

@if 1 {
  .store-top {
    position: relative;
    display: none;
    padding: 40px 0px 5% 0px;
    background-image: url(../img/bg.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom left;
  }


  .store {
    padding: 15px 0px 60px 0px;

    @media (min-width: 768px) {
      padding: 30px 0px 120px 0px;
    }
  }

  .store__title {
    margin-bottom: 30px;
  }

  .store__col-slider {
    position: relative;
    margin-bottom: 30px;
  }

  .store__prev,
  .store__next {
    position: absolute;
    top: 45%;
    color: $color-primary;
  }

  .store__prev {
    left: -30px;
  }

  .store__next {
    right: -30px;
  }

  .store__slider-control {
    display: none;
  }

  @media (min-width: 1350px) {
    .store__slider-control {
      display: block;
    }
  }

  .store__col-right {
    display: flex;
    flex-direction: column;
  }

  .store__row-icon {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }

  .store__icon {
    margin-right: auto;
    margin-bottom: 0;
  }

  .store__icon img {
    margin-top: 0;
  }

  .store__namber {
    display: none;
    margin-right: 20px;
    margin-left: 10px;
  }

  .store__namber::before {
    margin-right: 15px;
  }

  .store__col-link {
    order: 1;
    display: flex;
    flex-wrap: wrap;
  }

  .store__link {
    position: relative;
    display: block;
    width: 49%;
    padding-left: 20px;
    color: $color-primary;
  }

  .store__link::before {
    position: absolute;
    top: 3px;
    left: 0px;
    // width: 100%;
    // height: 100%;
  }

  .store__link-right {
    width: 100%;
    height: 40px;
    margin-top: 20px;
    margin-bottom: 30px;
    line-height: 36px;
    padding: 0 30px;
    border: 2px solid #6d71f9;
    border-radius: 20px;
    text-align: center;
  }

  .store__col-text {
    margin-bottom: 30px;
  }

  .store__col-text p {
    margin-bottom: 5px;
  }

  .store__col-text p:nth-of-type(n + 2) {
    display: none;
  }

  .store__btn-more {
    color: #b8b9d0;
    cursor: pointer;
    @media (pointer: fine) {
      &:hover {
        color: $color-hover;
      }
    }
  }

  .store__btn-more::after {
    // content: '\e908';
    // font-family: 'icomoon' !important;
    // vertical-align: middle;
    // color: #b8b9d0;
  }

  @media (min-width: 480px) {
    .store__namber {
      display: block;
    }
  }

  @media (min-width: 768px) {
    .store__row {
      display: block;
    }

    .store__col-slider {
      float: left;
    }

    .store__col-right {
      display: block;
    }

    .store__col-right:after {
      content: ' ';
      display: table;
      clear: both;
    }

    .store__col-icon,
    .store__col-link {
      float: right;
      width: 50%;
      padding-left: 10px;
    }

    .store__col-link {
      margin-bottom: 40px;
    }

    .store__link {
      width: 100%;
      margin-bottom: 20px;
    }

    .store__link-right {
      margin-top: 10px;
    }

    .store__col-text {
      float: left;
    }
  }

  @media (min-width: 1200px) {
    .store-top {
      display: block;
    }

    .store {
      padding: 60px 0px 60px 0px;
    }

    .store__title {
      text-align: center;
      margin-bottom: 60px;
    }

    .store__col-slider {
      margin-right: 8.333333%;
    }

    .store__col-right {
      display: flex;
    }

    .store__col-link {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .store__col-icon,
    .store__col-link {
      float: none;
      width: 100%;
      padding-left: 0;
    }

    .store__link {
      width: auto;
      margin: 0;
      transition: 0.2s ease 0s;
    }

    .store__link-right {
      width: auto;
      margin: 0;
      white-space: nowrap;
      display: flex;
      flex-wrap: wrap;
    }
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .store__link:hover {
      color: $color-hover;
    }

    .store__link-right:hover {
      border-color: $color-hover;
    }
  }
}



// ===== spoiler ===== 
  
@if 1 {
  .spoiler {
    padding: 0 15px;
  
    @media (min-width: 768px) {
      padding: 0;
      position: relative;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 100%;
    }
  
    @media (min-width: 1200px) {
      justify-content: center;
    }
  }
  
  .spoiler::before {
    @media (min-width: 768px) {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0px;
      // left: 0px;
      display: block;
      width: calc(100% - 30px);
      max-width: 1240px;
      height: 60px;
      margin: 0 15px;
      background: linear-gradient(109.5deg, #eeeef1 6.26%, #ffffff 93.74%);
      box-shadow: -12px -12px 24px #ffffff, 12px 12px 24px #e9e9ec;
      border-radius: 30px;
    }
  }
  
  .spoiler__btn {
    padding: 0 20px;
    cursor: pointer;
  
    @media (min-width: 768px) {
      flex: 1 0 0;
      justify-content: center;
      margin-bottom: 0;
      z-index: 2;
      // width: auto;
      max-width: 290px;
      padding: 0 50px;
      margin: 0 15px;
      // text-align: center;
      background: none;
      box-shadow: none;
    }
  }
  
  .spoiler__btn::before,
  .spoiler__btn::after {
    @media (min-width: 768px) {
      display: none;
    }
  }
  
  .spoiler__btn--xl {
    display: none;
  
    @media (min-width: 1200px) {
      display: block;
    }
  }
  
  .spoiler__btn.active,
  .tabs__btn.active {
    color: $color-primary;
  }
  
  .spoiler__btn.active::before,
  .tabs__btn.active::before {
    @media (min-width: 768px) {
      content: '';
      position: absolute;
      bottom: 0px;
      top: auto;
      left: 50%;
      display: block;
      width: calc(100% - 40px);
      height: 2px;
      background-color: $color-primary;
      transform: translateX(-50%);
    }
  }
  
  @media (pointer: fine),
  (min-width: 0\0) {
    .spoiler__btn:hover {
      background-color: #fff;
    }
  }
  
  
  
  
  
  .spoiler__panel {
    display: none;
    padding-top: 30px;
  
    @media (min-width: 480px) {
      padding-top: 40px;
    }
  
    @media (min-width: 768px) {
      order: 1;
      width: 100%;
    }
  
    @media (min-width: 1200px) {
      padding-top: 60px;
    }
  }
}



// ===== panel map =====

@if 1 {
  .shop__map {
    height: 480px;
    margin: -80px -15px 40px -15px;

    @media (min-width: 480px) {
      height: 600px;
    }

    @media (min-width: 768px) {
      height: 820px;
      margin: -80px -20px -155px -20px;
    }
  }

  .shop__map .gm-style-pbc+div {
    background-image: linear-gradient(0deg, rgba(245, 245, 248, 0) 50%, #f5f5f8 100%);
  }
}



// ===== panel reviews =====

@if 1 {
  .btn__reviews {
    width: 100%;
    margin: 30px 0px 20px 0px;
    text-align: center;
  }

  .reviews__btn-md {
    display: none;

    @media (min-width: 768px) {
      display: block;
      width: auto;
      padding: 0px 30px;
      margin: 0 auto 40px;
    }

    @media (min-width: 1200px) {
      display: none;
    }
  }

  .reviews__btn-modal {
    margin-bottom: 30px;

    @media (min-width: 768px) {
      display: none;
    }
  }

  .reviews__item {
    margin-bottom: 30px;
  }

  .reviews__rating {
    margin-bottom: 20px;
  }

  .reviews__item p {
    margin-bottom: 10px;
  }

  .reviews__date {
    color: #b8b9d0;
  }

  .reviews__form-wrap {
    display: none;
    margin-bottom: 30px;
  }

  @media (min-width: 768px) {
    .reviews__item {
      margin-bottom: 40px;
    }

    .reviews__rating {
      margin-bottom: 20px;
    }

    .reviews__item p {
      margin-bottom: 15px;
    }
  }
}



// ===== panel stock =====

@if 1 {
  .stock {
    margin-bottom: 30px;
  }

  .stock__slider-wrap {
    position: relative;
  }

  .stock__slider {
    margin: -10px;
  }

  .stock__slide {
    margin: 10px;

    &:nth-of-type(n + 2) {
      display: none;
    }

    @media (min-width: 1350px) {
      &:nth-of-type(n + 2) {
        display: block;
      }
    }
  }

  .stock__prev,
  .stock__next {
    position: absolute;
    top: 150px;
    display: none;
    color: $color-primary;

    @media (min-width: 1350px) {
      display: block;
    }
  }

  .stock__prev {
    left: -45px;
  }

  .stock__next {
    right: -45px;
  }

  .stock__row {
    @media (min-width: 768px) {
      display: flex;
      //  flex-wrap: wrap;
    }
  }

  .stock__col {
    margin-bottom: 30px;

    @media (min-width: 480px) {
      margin-bottom: 40px;
    }

    @media (min-width: 768px) {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }

  .stock__wrap-img {
    display: block;
    padding-bottom: 52%;
  }

  .stock__desc {
    position: relative;
    padding: 10px 0px 0px 0px;

    @media (min-width: 480px) {
      padding-top: 20px;
    }

    p {
      margin-bottom: 14px;
      padding-right: 110px;
    }
  }

  .stock__title {
    padding-right: 110px;
    margin-bottom: 12px;
    font-weight: 500;
    font-size: 18px;
  }

  .stock__day {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    padding: 0 14px;
    margin-bottom: 20px;
    color: #fff;
    line-height: 1;
    background: linear-gradient(104.98deg, #6266e0 0%, #7579ff 100%);
    box-shadow: 0px 12px 12px rgba(109, 113, 249, 0.25);
    border-radius: 5px;

    @media (min-width: 480px) {
      position: absolute;
      right: 0;
      top: -50px;
      flex-direction: column;
      width: 100px;
      height: 100px;
      padding: 10px;
    }
  }

  .stock__day span {
    font-weight: 500;
    font-size: 24px;
    color: $color-hover;
  }

  .stock__date {
    color: $color-disable;
    margin-bottom: 10px;
  }

  .stock__code {
    color: $color-disable;

    span {
      font-size: 18px;
      color: $color-primary;

      &::after {
        content: '\e911';
        font-family: 'icomoon' !important;
        color: #6d71f9;
        margin-left: 10px;
      }
    }
  }

  .stock__btn-more {
    @media (min-width: 1350px) {
      display: none;
    }
  }
}