// ===== btn ===== 

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 40px;
  padding: 0px 30px;
  // font-weight: 600;
  // color: #ffffff;
  // letter-spacing: 0.025em;
  // background-color: #ff7129;
  // box-shadow: 0px 10px 16px 0px rgba(255, 113, 41, 0.4);
  // border: 2px solid #6D71F9;
  border-radius: 20px;
  transition: .2s;

  @media (pointer: fine),
  (min-width:0\0) {
    &:hover {
      color: $color-hover;
    }
  }

  .icon {
    flex-shrink: 0;
  }
}




.btn-green {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  width: 100%;
  color: #6D71F9;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  text-shadow: 0px 0px 12px #B5FFFD;
  border: 4px solid transparent;
  border-radius: 25px;
  background-image: linear-gradient(180deg, #5CDEDA 0%, #2FFF6A 50%, #5CDEDA 100%), linear-gradient(0deg, #2FFF6A 0%, #5CDEDA 50%, #2FFF6A 100%);
  background-origin: border-box;
  background-clip: padding-box, border-box;
  box-shadow: 0px 12px 24px rgba(47, 255, 106, .6);
  background-size: 100% 200%;
  transition: .5s;

  @media (min-width: 768px) {
    height: 60px;
    border-radius: 30px;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    &:hover {
      background-position: 0 100%;
    }
  }

  &:active {
    color: #fff;
  }
}



.btn-oval {
  display: block;
  width: 100%;
  padding: 7px 10px;
  margin-bottom: 20px;
  text-align: center;
  border: 2px solid #6D71F9;
  border-radius: 20px;
  transition: .2s ease 0s;

  @media (pointer: fine),
  (min-width:0\0) {
    &:hover {
      border-color: $color-hover;
    }
  }
}


// btn-white

.btn-3 {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 50px;
  // padding: 0 20px;
  margin-bottom: 20px;
  text-align: left;
  background: linear-gradient(109.5deg, #eeeef1 6.26%, #ffffff 93.74%);
  box-shadow: -12px -12px 24px #ffffff, 12px 12px 24px #e9e9ec;
  border-radius: 30px;

  @media (min-width: 768px) {
    min-height: 60px;
  }
}



