// ==== General ==== 

body {
	// min-width: 320px;
	overflow-x: hidden;
	font-family: 'Rubik', sans-serif;
	font-size: 18px;
	font-weight: 300;
	line-height: 1.55;
	color: #2A2929;

	@media (max-width: 1200px) {
		font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1200 - 320)));
	}
}

body.lock {
	overflow: hidden;
}

.wrapper {
	padding-top: 60px;
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100%;
	background-color: #F5F5F8;
	// overflow: hidden;
}

@media (min-width: 768px) {
	.wrapper {
		padding-top: 80px;
	}
}

main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}

.page {
	padding: 15px 0px 60px 0px;

	@media (min-width: 768px) {
		padding: 30px 0px 120px 0px;
	}

	@media (min-width: 1200px) {
		padding-top: 60px;
	}
}

.img_cover {
	position: absolute;
	height: 100%;
	width: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;';
}

._cover {
	position: relative;
}

._cover img {
	position: absolute;
	height: 100%;
	width: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;';
}

.visuallyhidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
}

.hidden {
	display: none !important;
}

*:last-child {
	margin-bottom: 0;
}


p {
	margin-bottom: 10px;
}


.mb-60 {
	margin-bottom: 60px;

	@media (max-width: 1200px) {
		margin-bottom: calc(30px + (60 - 30) * ((100vw - 320px) / (1200 - 320)));
	}
}
.mb-40 {
	margin-bottom: 40px;

	@media (max-width: 1200px) {
		margin-bottom: calc(15px + (40 - 15) * ((100vw - 320px) / (1200 - 320)));
	}
}

 


.mr-10 {
	margin-right: 10px;
}


.box-icon {
	display: flex;
	width: 100%;
}

.box-icon img:not(:last-child) {
	margin-right: 10px;
}

.round {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	margin-left: auto;
	border-radius: 50%;
	background-color: $color-hover;
}